import axios from 'axios'
//constants
import {
    LOGIN,
    LOGOUT
   } from '../constants'
   

   export const setUser = (userData)=>({type: LOGIN, payload: userData})
   
   export const setLogout = () => (dispatch) => {
     return dispatch({ type: LOGOUT, payload: {} })
   }
   export const registerUser = (user) =>{
    axios.post("/api/user/register", user).then((u)=>{console.log(u)}).catch((e)=>e)
   } 
   
   export const login = ({email,password}) => async (dispatch)=>{
      
  
       try{
         const user = await axios.post("/api/user/login", {email, password})
         dispatch(setUser(user.data))
       }catch(err){
         return err
       }
       
   }
   
   export const logout = () => async (dispatch)=>{
     try{
       const user = await axios.post("/api/user/logout")
       dispatch(setLogout(user.data))
     }catch(err){
       return "err"
     }
     
   }
   
   
   
   
   
   
   